import React, { useState,useEffect} from 'react'
import './UserList.css'
// import { DataGrid } from '@mui/x-data-grid';
// import {DeleteOutline} from '@material-ui/icons';
import CircularProgress from '@mui/material/CircularProgress';
// import {UserRows} from '../../DummyData'
import {getAllUsers} from '../../components/utils/api'
import {getAminUserToken} from '../../components/utils/Common'

import { Link } from 'react-router-dom'; 


import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function UserList() {
    const [data, setData] = useState([]);
    useEffect(() => {
        const AllUsers =  async ()=>{
            const userAccessToken = getAminUserToken(); 
             await getAllUsers(userAccessToken).then(res=>{
                const DataResult = res.data.data.categories;  
                setData(DataResult); 
                console.log(data);
            }).catch(err=>{
                console.log(err)
            })       
        }
        AllUsers();
    }, [data]);

    const handleDelete = (id)=>{  setData(data.filter((item)=>item._id !==id)) }

    return (
        
        <div className="userList" style={{ height: 400, width: '100%', margin:20}}>
           <h1 className="usersTitle">Users</h1>
           {!data || data.length <=0? <CircularProgress /> :  
            <TableContainer component={Paper} style={{width:1000, height:400}}>
                <Table sx={{ minWidth: 600 }} aria-label="simple table">
                  <TableHead style={{backgroundColor:'black'}}>
                    <TableRow>
                      <TableCell style={{color:'white', fontWeight:'bold'}}>ID</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}}>Account Type</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Name</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Email</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Phone</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">BvnVerified</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Confirmed</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Deactivated</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Devices</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Payment Cards</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">OTP</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">OTP Expiry</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Smart Card Interest</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Usercode</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">CreatedAt</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">UpdatedAt</TableCell>
                      <TableCell  style={{color:'white', fontWeight:'bold'}} align="right">Action</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {data.map((user,index) => (
                      <TableRow key={user._id} sx={{ '&:last-child td, &:last-child th': { border: 0 } }} >
                        <TableCell component="th" scope="row">{index+1}</TableCell>
                        <TableCell>{user.accountType}</TableCell>
                        <TableCell>{user.name}</TableCell>
                        <TableCell>{user.email}</TableCell>
                        
                        <TableCell>{user.phone}</TableCell>
                        <TableCell>{user.bvnVerified ? "Verified" :"Unverified"}</TableCell>
                        <TableCell>{user.confirmed   ? "Confirmed" :"Unconfirmed"}</TableCell>
                        <TableCell>{user.deactivated ? "Deactivated" :"Active"}</TableCell>
                        {/* last_4digits */}
                        <TableCell>{user.devices ? user.devices.map((dev,index)=>(dev.deviceId)) : "NA"}</TableCell>
                        <TableCell>{user.paymentCards ? user.paymentCards.map((card,i)=>(card.last_4digits)) :"NA"}</TableCell>
                        <TableCell>{user.otp}</TableCell>
                        <TableCell>{user.otpExpiry}</TableCell>
                        <TableCell>{user.smartMeterInterest ? "Yes" :"No"}</TableCell>
                        
                        <TableCell>{user.userCode}</TableCell>
                        <TableCell>{user.createdAt}</TableCell>
                        <TableCell>{user.updatedAt}</TableCell> 
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
           }

        </div>
    )
}
